<template>
	<view class="sellDetail">
		<view class="warp">
			<view class="sellCard">
				<view  class="header">
					<swiper  class="ads" autoplay  indicator-dots>
						<swiper-item  v-for="(item, index) in ads" :key='index'>
							<image @click="gotoShowImage(index)" mode="aspectFill" :src="item"></image>
						</swiper-item>
					</swiper>
				</view>
				<view class="detail">
					<span class="weight_600 bt-front-16">闲置简介</span>
					<view class="mg10"><text>{{sellItem.content}}</text></view>
					<view @click="copy()" class="about mg10"><span>联系方式</span></view>
					<span class="mg10" ><text @click="copy()">{{sellItem.contact}}</text></span>
				</view>
			</view>
			<view class="sell-comment">
				<view class="comment-title l_r">
					<view class="left">
						<view class="item">
							<span>评论区</span>
							<myXhx :width="60" ></myXhx>
						</view>
					</view>
					<view class="right">
						<view v-if="getStatusLab == '待发货' && sellItem.userId==getUserId"  class="item">
							<!-- 发货按钮 -->
							<uni-icons  @click="()=>{this.$refs.fhSellPoput.open()}" color="#ffd33f"   type="paperplane" size="22"></uni-icons>
							<!-- <image   style="margin-left: 8px;width: 20px;height: 20px;"  class="item"  src="../../static/images/icon/delete.png"></image> -->
						</view>
						<view v-if="getStatusLab == '已发货' && sellItem.userId==getUserId"  class="item">
							<!-- 完成按钮 -->
							<uni-icons  @click="()=>{this.$refs.wcSellPoput.open()}"  color="#ffd33f"  type="checkbox-filled" size="22"></uni-icons>
							
							<!-- <image   @click="()=>{this.$refs.wcSellPoput.open()}" style="margin-left: 8px;width: 20px;height: 20px;"  class="item"  src="../../static/images/icon/delete.png"></image> -->
						</view>
					
						<!-- 购买按钮 -->
						<view  v-if="getStatusLab == '未卖出'" class="item">
							<uni-icons color="#ffd33f"  @click="()=>{this.$refs.uptateSellPoput.open()}" type="cart-filled" size="22"></uni-icons>
							<!-- <image  @click="()=>{this.$refs.uptateSellPoput.open()}" style="margin-left: 8px;width: 20px;height: 20px;"  class="item"  src="../../static/images/icon/like-active.png"></image>							 -->
						</view>
						<!-- 删除按钮 -->
						<view  v-if="getUserId&&sellItem.userId==getUserId" class="item">
							<image  @click="()=>{this.$refs.deleteSellPoput.open()}" style="width: 20px;height: 20px;"  class="item"  src="../../static/images/icon/delete.png"></image>							
						</view>
					</view>
				</view>
				
				<view class="comment-content">
					<span>评论区未开启</span>
				</view>
				
			</view>
		</view>
		
		
		<!-- 删除确认框 -->
		<my-poput ref="deleteSellPoput" @submitForm="deleteSell()" >
			<template slot="title">
				<span>删除</span>
			</template>
			<template slot="content">
				<span>确定删除该闲置？</span>
			</template>
		</my-poput>
		
		<!-- 购买确认框 -->
		<my-poput ref="uptateSellPoput" @submitForm="updateSell(2)" >
			<template slot="title">
				<span>购买</span>
			</template>
			<template slot="content">
				<span>确定购买该闲置？</span>
			</template>
		</my-poput>
		
		<!-- 发货确认框 -->
		<my-poput ref="fhSellPoput" @submitForm="updateSell(3)" >
			<template slot="title">
				<span>发货</span>
			</template>
			<template slot="content">
				<span>是否发货？</span>
			</template>
		</my-poput>
		
		<!-- 完成-->
		<my-poput ref="wcSellPoput" @submitForm="updateSell(4)" >
			<template slot="title">
				<span>完成</span>
			</template>
			<template slot="content">
				<span>是否确认完成订单？</span>
			</template>
		</my-poput>
		
	</view>
</template>

<script>
	import {getSellById,deleteSellById,updateSellById} from "@/api/sell.js"
	import {mapGetters} from 'vuex'
	import myPoput from "@/components/myPoput/myPoput.vue"
	export default {
		data() {
			return {
				sellId:"",
				ads: [
				
				],
				sellItem:{}
			}
		},
		components:{
			myPoput
		},
		onLoad(op) {
			
			this.sellId = op.sellId
			
			this.getSell();
		},
		computed:{
			...mapGetters('user',['getUserId']),
			getStatusLab(){
				const status = this.sellItem.status;
				if(status == '1')
					return "未卖出";
				if(status == '2')
					return "待发货";
				if(status == '3')
					return "已发货";
				if(status == '4')
					return "交易完成";
				if(status == '5')
					return "已发货";
				return "未卖出"
			}
		},
		methods: {
			gotoShowImage(index){
				this.fullScreen(index);
			},
			deleteSell(){
				
				deleteSellById(this.sellId).then((res)=>{
				
					// uni.navigateBack();
				})
				uni.showToast({//提示
					title:'购买成功'
				})
				uni.navigateBack();
			},
			updateSell(status){
				updateSellById({
					id:this.sellId,
					status:status
				}).then((res)=>{
					// uni.navigateBack();
				})
				uni.navigateBack();
			},
			//全屏预览图片函数方法
			fullScreen(i) {
				wx.previewImage({
					urls: this.ads, //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					current: i, // 当前显示图片的http链接，默认是第一个
					success: function(res) {console.log("打开成功")},
					fail: function(res) {console.log("打开失败")},
					complete: function(res) {console.log("完成打开")},
				})
			},
			getSell(){
				const param = {
					sellId : this.sellId
				}
				getSellById(param).then(res=>{
					this.sellItem = res.data
					if(res.data.sellPictures!=null)
				
						res.data.sellPictures.forEach(item=>{
							this.ads.push(this.getUrl(item.url))
						})
				
				})
				
			},
			copy(){
				
				  uni.showModal({
					title:"联系方式",
					content:this.sellItem.contact,//模板中提示的内容
					confirmText:'复制',
					success:()=>{//点击复制内容的后调函数
					  //uni.setClipboardData方法就是讲内容复制到粘贴板
					  uni.setClipboardData({
						data:this.sellItem.contact,//要被复制的内容
						success:()=>{//复制成功的回调函数
						  uni.showToast({//提示
							title:'复制成功'
						  })
						}
					  });
					}
				  });
			
			}
		}
	}
</script>

<style>
.sellDetail{
	padding-top: 1px;
}
.sellCard image{    
	
	width: 100%;
	
}
.ads{
	width: 100%;
	height: 350px;
}
.ads image{
	height: 100%;
	width: 100%;
}
/* 简介。联系方式 */
.detail{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 10px;
	padding: 0 5px 50px 5px;
}
.mg10{
	margin-top: 10px;
}
.about{
	padding: 5px 8px;
	background-color: #ffd33f;
	border-radius: 13px;
	display: flex;
	align-items: center;
	justify-content: center;
}

/* 评论取区 */
.sell-comment{
	width: 100%;
	padding: 50px 0px;
}
.comment-title > .left > .item{
	display: flex;
	flex-direction: column;
	align-items: center;
}
.comment-content{
	display: flex;
	justify-content: center;
	margin-top: 20px;
}

</style>
